<template>
  <div class="eleme">
      <h1 class="large_t">查找</h1>
      <div class="search">
        <!-- <form @submit="search()"> -->
            <input @keypress="search($event)" type="search" class="search_box" @focus="isState=true" v-model="keyword" :class="{focus:keyword!=undefined&&isState==true}" placeholder="想吃点什么呢？">
        <!-- </form> -->
        <span class="ico iconfont icon-sousuo1"></span>
        <span class="iconfont icon-guanbi ico_x" v-show="keyword!=undefined&&keyword!=''" @click="keyword=''"></span>
        <span class="cancle two_t_m" v-show="keyword!=undefined&&isState==true" @click="keyword=undefined;isState=false;">取消</span>
      </div>
      <div v-show="!isState">
        <div class="series">
            <ul class="clearfix">
                <li v-for="(item,index) in series_list" :key="index" class="fl">
                    <img src="../../assets/test/goods.png" alt="">
                    <p class="three_t_h">{{item.name}}</p>
                </li>
            </ul>
        </div>
        <div class="allGoods">
            <ul>
                <li class="two_t_m clearfix" @click="$router.push('/recommend')">所有产品
                    <span class="fr">></span>                  
                </li>
            </ul>
        </div>
        <div class="lastestDiscount">
            <ul>
                <li class="two_t_m clearfix">最新优惠
                    <span class="fr">></span>                                    
                </li>
            </ul>
        </div>
      </div>
      <div v-show="isState" class="searchList">
          <p class="clearfix">
              <span class="logo fl"></span>
              <span class="two_t_h fl">最受欢迎</span>
          </p>
          <ul>
              <li v-for="(item,index) in search_list" :key="index" @click="$router.push('/detail')" class="search_item">
                  {{item.name}}
                  <span class="dian"></span>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
export default {
  name: "Eleme",
  data() {
    return {
      series_list: [
        {
          name: "芒果系列"
        },
        {
          name: "蛋白系列"
        },
        {
          name: "大瓶系列"
        },
        {
          name: "尊享礼盒"
        },
        {
          name: "套餐系列"
        },
        {
          name: "得乐周边"
        }
      ],
      keyword: undefined,
      isState: false,
      search_list: [
          {
              name: '芒果酸奶'
          },
          {
              name: '草莓酸奶'
          },
          {
              name: '蓝莓酸奶'
          },
          {
              name: '火龙果燕麦酸奶'
          }
      ]
    };
  },
  methods: {
    //键盘回车事件
    search(e){
      if(e.keyCode=='13'){
        e.preventDefault(); 
        this.$router.push('/search/searchlist')              
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.eleme {
  font-size: 0.16rem;
  // 搜索框
  .search {
    font-size: 0.16rem;
    position: relative;
    margin: 0.3rem 0rem;
    .search_box {
      width: 3.2rem;
      height: 0.4rem;
      border: none;
      line-height: 0.41rem;
      background: rgba(0, 0, 0, 0.05);
      border-radius: 0.06rem;
      padding-left: 0.43rem;
      padding-right: 0.35rem;
    }
    .focus {
      width: 2.8rem;
      transition: width 0.3s;
    }
    .ico_x {
      font-size: 0.18rem;
      color: #ccc;
      position: absolute;
      top: 0.11rem;
      right: 0.55rem;
    }
    .cancle {
      border: none;
      position: absolute;
      top: 0.08rem;
      right: 0rem;
    }
    .ico {
      position: absolute;
      top: 0.11rem;
      left: 0.15rem;
      color: rgba(0, 0, 0, 0.5);
      font-size: 0.18rem;
    }
  }
  // 系列分组
  .series {
    ul {
      li {
        margin-bottom: 0.2rem;
        img {
          display: block;
          width: 1rem;
          height: 1rem;
          border-radius: 0.12rem;
          margin-bottom: 0.05rem;
        }
        &:nth-child(3n + 2) {
          margin: 0rem 0.1rem;
        }
      }
    }
  }
  .allGoods {
    li {
      height: 0.5rem;
      line-height: 0.5rem;
      border-bottom: 0.01rem solid rgba(0, 0, 0, 0.05);
    }
  }
  .lastestDiscount {
    li {
      height: 0.5rem;
      line-height: 0.5rem;
    }
  }
  .searchList {
      p {
          margin-bottom: 0.25rem;
          .logo {
              width: 0.16rem;
              height: 0.16rem;
              background-image: url('../nearby/le.png');
              background-size: cover;
              margin-right: 0.06rem;
              margin-top: 0.03rem;
          }
      }
      ul {
          .search_item {
              position: relative;
              padding-left: 0.2rem;
              height: 0.52rem;
              line-height: 0.52rem;
              border-bottom: 0.01rem solid #f2f2f2;
              .dian {
                  position: absolute;
                  width: 0.03rem;
                  height: 0.03rem;
                  border-radius: 50%;
                  left: 0rem;
                  top: 0.25rem;
                  background-color: #7f7f7f;
              }
          }
      }
  }
}
</style>



