export default {
  name: "Eleme",
  data() {
    return {
      series_list: [
        {
          name: "芒果系列"
        },
        {
          name: "蛋白系列"
        },
        {
          name: "大瓶系列"
        },
        {
          name: "尊享礼盒"
        },
        {
          name: "套餐系列"
        },
        {
          name: "得乐周边"
        }
      ],
      keyword: undefined,
      isState: false,
      search_list: [
          {
              name: '芒果酸奶'
          },
          {
              name: '草莓酸奶'
          },
          {
              name: '蓝莓酸奶'
          },
          {
              name: '火龙果燕麦酸奶'
          }
      ]
    };
  },
  methods: {
    //键盘回车事件
    search(e){
      if(e.keyCode=='13'){
        e.preventDefault(); 
        this.$router.push('/search/searchlist')              
      }
    }
  },
  components: {}
};



